body {
  font-family: sans-serif;
}



.MainContainer {
  padding: 4% 12%;
  margin-top: 32px;
  box-sizing: border-box;

  width: 100%;
  display: flex;
  align-items: center;

}

.ProductPreview {

  width: 50%;
  position: relative;

}


.ProductData {
  width: 50%;
}

@media screen and (max-width: 768px){
  .MainContainer {
    padding: 4% 12%;
    margin-top: 32px;
    box-sizing: border-box;
  
    width: 100%;
    display:inline-block;
    align-items: center;
  
  }
  .ProductPreview {

    width: 100%;
    position: relative;
  
  }
  
  
  
  .ProductData {
    width: 100%;
  }

 }


 
