
  .ProductPreview {
  
    width: 100%;
    position: relative;
  
  }
  
  .ProductPreview>img {
    width: 100%;
  }
  

  
  .TimeSection {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 48px;
    font-weight: 600;
  }
  
  .HeartBeatSection {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 48px;
    font-weight: 600;
  }
  
  .HeartBeatSection>p {
    margin-top: 16px;
  }
  
  .HeartBeatSection>i {
    color: red;
    animation: beat 1s infinite;
  }
  
  /* Animacion*/
  
  @keyframes beat {
    0% {
      transform: scale(1, 1);
    }
  
    50% {
      transform: scale(1.2, 1.2);
    }
  
    100% {
      transform: scale(1, 1);
    }
  
  }

  @media screen and (max-width: 768px){
    .TimeSection {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: white;
      font-size: 38px;
      font-weight: 600;
    }
    
    .HeartBeatSection {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: white;
      font-size: 38px;
      font-weight: 600;
    }

  }