

.ProductData {
    width: 100%;
  }
  
  .ProductTitle {
    font-size: 48px;
    margin-top: 0;
    margin-bottom: 0px;
  
  }
  
  .ProductDescription {
  
    font-size: 20px;
    color: rgb(0, 0, 0, 0.8);
  }
  
  .sectionHeading {
    font-size: 28px;
    margin-bottom: 8px;
  
  }
  
  .ProductImage {
    width: 100%;
    max-width: 80px;
  
    border: 2px solid transparent;
    border-radius: 8px;
    margin-right: 12px;
    cursor: pointer;
  
  }
  
  .SelectedProductImage {
    border: 2px solid teal;
  
  }
  
  .FeatureItem {
    background-color: gainsboro;
    border-radius: 8px;
    border: 0;
  
    padding: 12px 24px;
    text-align: center;
    margin-right: 12px;
    margin-bottom: 8px;
    cursor: pointer;
    font-weight: 600;
    height: 30%;
  
  }
  
  .SelectedItem {
    color: white;
    background-color: gray;
  }
  
  .PrimaryButton {
    color: white;
    background-color: rgb(255, 166, 0);
    font-size: 20px;
    border-radius: 8px;
    border: 0;
    padding: 12px 24px;
  
  }